<template>
    <div class="mfa-modal">
        <Form ref="mfaForm" :model="form" :rules="rules" @keydown.enter.native="handleSubmit">
            <FormItem prop="dynamic">
                <Input type="text" :maxlength=6 v-model="form.dynamic" autofocus="autofocus" placeholder="">
                    <span slot="prepend">
                    <Icon :size="14" type="md-key"></Icon>
                    </span>
                </Input>
            </FormItem>
        </Form>
        <div class="mfa-modal-footer">
            <Button @click="cancel"></Button>
            <Button @click="handleSubmit" type="primary"></Button>
        </div>
    </div>

</template>
<script>
import {
  dynamic
} from '@/api/user'
import Cookies from 'js-cookie'
import config from '@/config'
const { cookieExpires } = config
export default {
  name: 'MfaInput',
  props: {
    dynamicRules: {
      type: Array,
      default: () => {
        return [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', min: 6, max: 6, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  data () {
    return {
      form: {
        dynamic: ''
      }
    }
  },
  computed: {
    rules () {
      return {
        dynamic: this.dynamicRules
      }
    }
  },
  methods: {
    handleSubmit (val) {
      this.$refs.mfaForm.validate(valid => {
        if (valid) {
          dynamic({ dynamic: this.form.dynamic }).then(res => {
            const { code, msg, data } = res.data
            if (code === 0) {
              // const { mfa_key } = data
              // console.log(location)
              // Cookies.set('mfa_key', mfa_key, { domain: '/', expires: cookieExpires || 1 })
              this.$Modal.remove()
            } else {
              this.$Message.error(msg)
            }
          }).catch(err => {
            reject(err)
          })
        }
      })
    },
    cancel (val) {
      console.log('this', this)
      this.$Modal.remove()
    }
  }
}
</script>
<style scoped>
    .mfa-modal-footer {
        position: absolute;
        bottom: -65px;
        padding: 5px 0;
        box-sizing: border-box;
        width: 100%;
        background: #fff;
        display: flex;
        gap: 5px;
        justify-content: flex-end;
    }
</style>
